.c-testimonials {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  height: 300px;
  width: 100%;
  overflow: hidden; }
  .c-testimonials:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    border-radius: 50%;
    background: #FBE3D5;
    width: 300%;
    padding-bottom: 300%; }
  .c-testimonials .testimonials-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 500px;
    color: #DF976F;
    text-align: center; }
    .c-testimonials .testimonials-content > * {
      margin: 8px 0 0; }
      .c-testimonials .testimonials-content > *:first-child {
        margin: 0; }



