.c-footer {
  padding: 16px 0; }
  .c-footer .footer-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between; }
  .c-footer .footer-logo {
    width: 125px;
    height: 50px;
    fill: #999999; }
    .c-footer .footer-logo span {
      display: none; }
    .c-footer .footer-logo svg {
      width: 100%;
      height: 100%; }
  .c-footer .footer-nav {
    font-size: 11px; }
    .c-footer .footer-nav ul {
      display: -ms-flexbox;
      display: flex; }
      .c-footer .footer-nav ul li {
        padding: 0 0 0 10px;
        margin: 0 0 0 10px; }
        .c-footer .footer-nav ul li:first-child {
          margin: 0;
          padding: 0; }
          .c-footer .footer-nav ul li:first-child:after {
            display: none; }
        .c-footer .footer-nav ul li:before {
          display: none; }
        .c-footer .footer-nav ul li:after {
          content: "";
          display: block;
          width: 1px;
          height: 75%;
          position: absolute;
          top: 50%;
          left: 0;
          background: #999999;
          transform: translate(0, -50%); }



