.c-hero {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  height: 90vh;
  width: 115vw;
  border-radius: 0 0 50% 50%;
  background-image: url("/static/images/hero.jpg");
  background-position: left;
  background-size: cover; }
  .c-hero .hero-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 550px;
    color: #FFF;
    text-align: center; }
    .c-hero .hero-content > * {
      margin: 8px 0 0; }
      .c-hero .hero-content > *:first-child {
        margin: 0; }



